*{
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
body{
    background-color: #fff;
    overflow-x: hidden;
    overflow: hidden;
    width:100%;
    color:#2e2e2e;
    font-family: sans-serif;
}
html{
    width: 100%;
    font-family: sans-serif;
}

a{
    text-decoration: none;
    color:#fff;

}
#root{
    width:100%;
    overflow:hidden;
}
a:visited{
    text-decoration: underline;
    color:#fff;
}
.notice_box{
    position: fixed;
    width:400px;
    height:40px;
    background: rgb(253, 109, 51);
    border-radius: 20px;
    padding:5px;
    color:rgb(226, 226, 226);
    margin:10px;
    bottom: 0px;
}






